import Bmob from "hydrogen-js-sdk";
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'

let that;
let search_text;
let uid;
export default {
	components: {
		uniNavBar,
	},
	data() {
		return {
			people: [], //获得人员数组
			is_producer: false,
			is_producer: false,
			showOrder: false, //是否显示排序
			showOptions:false,
			type: '', //选择类型,
			producerHeader: {
				num: 0,
				debtMoney: 0
			},

			headerSelection: {
				order: {
					"order": "-createdAt"
				},
				options: '',
			},
			orders: [{
				"desc": "应付欠款",
				"notice": "从高到低",
				"order": "-debt",
				"checked": false,
			}, {
				"desc": "应付欠款",
				"notice": "从低到高",
				"order": "debt",
				"checked": false,
			}, {
				"desc": "创建日期",
				"notice": "最新",
				"order": "-createdAt",
				"checked": true,
			}, {
				"desc": "创建日期",
				"notice": "最晚",
				"order": "createdAt",
				"checked": false,
			}, {
				"desc": "名字",
				"notice": "正序",
				"order": "goodsName",
				"checked": false,
			}]
		}
	},
	onLoad(options) {
		that = this;
		uid = uni.getStorageSync('uid');

		if (options.type == "producer" || options.type == "producerFinance") {
			that.is_producer = true;
			that.load_data("producers")
		}

		that.type = options.type
	},

	onShow() {
		uni.removeStorageSync("producers")
		uni.removeStorageSync("producer_type")
		that.load_data("producers")
	},
	onUnload() {
		search_text = ""
	},
	methods: {

		//头部的options选择
		selectd(type) {
			if (type == "order") {
				that.showOrder = true
			} else if (type == "options") {
				that.showOptions = true
			}
		},
		
		//选择当前排序
		selectOrder(item){
			for(let item of that.orders){
				item.checked = false
			}
			item.checked = true
			that.headerSelection.order = item
			that.load_data("producers")
		},

		//打电话
		makePhoneCall(phone) {
			if (phone) {
				uni.makePhoneCall({
					phoneNumber: phone //仅为示例
				});
			} else {
				uni.showToast({
					icon: "none",
					title: "未填写联系电话"
				})
			}
		},
		
		//点击店仓去到详情
		gotoDetail(item) {
			if (that.is_producer) {
				return
			}
			uni.navigateTo({
				url: 'producer_detail/producer_detail?id='+item.objectId
			})
		},

		//选择此供货商
		select_this(e) {
			let producer =JSON.parse(e.detail.value);
			if (that.type == "producerFinance") {
				if (producer.debt == 0 || producer.debt == '') {
					uni.showToast({
						icon: "none",
						title: "没有欠款"
					})
					return
				}
			}

			uni.setStorageSync("producer", producer)
			uni.navigateBack({
				delta: 1
			})
		},

		//前去添加客户
		goto_add() {
			uni.navigateTo({
				url: "add/add?type=producers"
			})
		},

		//输入内容筛选
		input_confirm(e) {
			search_text = e.detail.value
			that.load_data("producers")
		},
		
		//modal重置的确认点击
		option_reset() {
			that.headerSelection = {
				order: {"order":"-createdAt"},
				options: '',
			};
			that.orders =  [{
				"desc": "应付欠款",
				"notice": "从高到低",
				"order": "-debt",
				"checked": false,
			}, {
				"desc": "应付欠款",
				"notice": "从低到高",
				"order": "debt",
				"checked": false,
			}, {
				"desc": "创建日期",
				"notice": "最新",
				"order": "-createdAt",
				"checked": true,
			}, {
				"desc": "创建日期",
				"notice": "最晚",
				"order": "createdAt",
				"checked": false,
			}, {
				"desc": "名字",
				"notice": "正序",
				"order": "goodsName",
				"checked": false,
			}]
			that.load_data("producers")
		},

		//加载数据
		load_data(type) {
			uni.showLoading({title:"加载中..."});
			that.producerHeader.num = 0;
			that.producerHeader.debtMoney = 0;
			
			const query = Bmob.Query(type);
			query.equalTo("parent", "==", uid);
			query.order(that.headerSelection.order.order);
			query.limit(500);
			if (search_text) {
				query.equalTo("producer_name", "==", {
					"$regex": "" + search_text + ".*"
				});
			}
			query.find().then(res => {
				console.log(res)
				uni.hideLoading();
				that.people = res;

				for (let item of res) {
					that.producerHeader.num += 1;
					that.producerHeader.debtMoney += item.debt || 0
				}
			});
		},

	}
}
